.homepage__wrapper {
  display: flex;
}

.homepage__wrapper {
  margin-top: 10%;
  justify-content: center;
}

.homepage__name {
  font-size: 5em;
}

.homepage__role {
  text-align: center;
  font-size: 25px;
}

.footer__author,
.homepage__role {
  color: #A9A9AC;
}

@media (max-width: 500px) {
  .homepage__name__wrapper {
    flex-direction: column;
  }

  .homepage__location {
    text-align: center;
  }

  .homepage__info__wrapper {
    margin-top: 0;
  }

  .homepage__location {
    text-align: center;
  }
}

@media (max-width: 400px) {
  .homepage__name,
  .homepage__surname {
    font-size: 6em;
  }
}
